  doGeneric(page) {
    this.user$.subscribe((u) =>{
      if(u != null && u.id != undefined && u.id != '') {
        this.user = u;
        this.config.getPageDetails(u.role, page).valueChanges().subscribe((p) => {
          if(p != undefined && p.length > 0) {
            let f = p[0]['pages'].find((pa) => {
              return pa.name == page
            });
            if(f != undefined) {
              console.log(f);
              this.fields = f.fields;
              this.title = f.title;
            } else {
              this.fields = this.activityTemplates[1].fields;
              this.title = this.activityTemplates[1].title;
            }

            // this.fields = p[0];
          } else {
            if(page = "addActivityAirportTransfer") {
              this.fields = this.activityTemplates[1];
            }
          }
        });
      }
    });
  }
